.header-container {
  width: 50%;
  display: flex;
  margin: 0 auto;
  height: 105px;
  align-items: center;
  justify-content: space-around; }
  .header-container .header-logo {
    width: 28%;
    padding-left: 20px; }
    .header-container .header-logo img {
      width: 65%; }
  .header-container .header-info {
    width: 30%;
    font-family: "Lato bold", sans-serif;
    display: inherit;
    flex-direction: column;
    align-items: flex-end; }
    .header-container .header-info .header-phone {
      font-size: 22px;
      margin: 0; }
    .header-container .header-info .header-working-hours {
      font-size: 12px;
      color: #676764;
      margin: 0; }
  .header-container .header-icons {
    width: 10%;
    padding-left: 10px; }
    .header-container .header-icons img {
      width: 35%;
      padding-left: 3px; }
  .header-container .header-link-schedule {
    width: 20%;
    height: 40px;
    border-radius: 35px;
    border: 2px solid #fae307;
    margin-right: 6px; }
    .header-container .header-link-schedule a {
      font-family: "Lato bold", sans-serif;
      font-size: 16px;
      color: #000000; }
