.footer {
  width: 58%;
  height: 360px;
  margin: 0 auto;
  background-color: #ebebeb; }
  .footer-container {
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-family: "Lato bold", sans-serif; }
  .footer-main-info p {
    font-size: 24px;
    margin: 0;
    padding-bottom: 7px; }
    .footer-main-info p:first-child {
      font-size: 28px;
      padding-top: 11px; }
    .footer-main-info p:last-child {
      font-size: 14px;
      color: #676764; }
  .footer-main-info, .footer-icons {
    text-align: center; }
  .footer-link-to-main h2 {
    margin: 0;
    padding-top: 35px; }
  .footer-link-to-main a {
    color: #000000; }
  .footer-icons {
    padding-left: 3px; }
    .footer-icons img {
      width: 25%; }
      .footer-icons img:last-child {
        padding-left: 4px; }
  .footer-link-to-policy {
    font-size: 14px; }
    .footer-link-to-policy a {
      color: #676764; }
